.contactsBlock {
  height: 100vh; }

.contactsContainer {
  flex-direction: column;
  justify-content: space-around; }

.contactsForm {
  height: 50%;
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }


.input {
  padding: 0 0;
  border-width: 0; }

.smallInput {
  width: 100%;
  height: 10%; }

.textArea {
  width: 100%;
  min-height: 75%;
  resize: none; }

