@import "../common/styles/sass/variables.sass";

.mainBlock {
  height: 100vh; }

.greeting {
  width: 300px;

  .nikita {
    color: $accentColor; }

  span {
    color: white; }

  h1 {
    color: white;
    font-size: 50px;
    letter-spacing: -0.2px;
    margin-bottom: 43px;
    font-weight: 300; }

  p {
    color: white; } }

.photo {
  width: 300px;
  height: 300px;
  background-image: url("https://wpriverthemes.com/drake/wp-content/themes/drake/assets/images/me.jpg");
  border-radius: 30px;
  background-size: cover; }

.container {
  justify-content: space-evenly;
  border: 1px solid #565656;
  border-radius: 30px;
  @media screen and (max-width: 752px) {
    justify-content: center; } }

